
.Details{
  display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}
.Details ul{
  text-align: left;
}
.reset{
    border-radius: 12px;
    border: 3px outset #888;
    position: relative;
    display: inline-block;
    padding: 12px 30px;
    color: #eee;
    text-transform: uppercase;
    letter-spacing: 4px;
    overflow: hidden;
    box-shadow: 0 0 10px rgb(0 0 0);
    font-family: verdana;
    font-size: 15px;
    font-weight: bolder;
    text-decoration: none;
    background: linear-gradient(160deg, #666, #444);
    text-shadow: 0px 0px 2px rgb(0 0 0 / 50%);
    cursor: pointer;
    margin-bottom: 30px;
  }
.Details p:first-child {
  width: 100px;
    height: 100px;
    border-radius: 50%;
    border: 6px solid #8f8f8f;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 26px;
}
.logout{
  color: #fff;
    position: absolute;
    top: 2%;
    right: 2%;
    background: transparent;
    border: 1px solid #767879;
    padding: 7px 10px;
    text-decoration: none;
    opacity: 0.7;
    cursor: pointer;
}
.logout:hover{
  opacity: 1;
}
.catTable{
  width: 100%;
}
.catTable td, .catTable th{
  padding: 5px;
}
.articles{
  border: 1px solid grey;
  margin-top: 10px;
  padding: 0px 20px;
  box-sizing: border-box;
}
