.otp {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.otpForm {
    background: rgba(0,0,0,.5);
    border-radius: 10px;
    box-shadow: 0 15px 25px rgb(0 0 0 / 60%);
    box-sizing: border-box;
    width: 40%;
    padding: 40px;
  }
  .otpForm .submit {
      background: #048188;
      border: 0;
      color: #fff;
      cursor: pointer;
      display: inline-block;
      font-size: 16px;
      letter-spacing: 4px;
      margin-top: 40px;
      overflow: hidden;
      padding: 10px 20px;
      position: relative;
      text-decoration: none;
      text-transform: uppercase;
  }
  .otpForm h2{
    font-size: 25px;
  }
  .otpForm input{
    display: inline-block;
    font-size: 20px;
    letter-spacing: 4px;
    overflow: hidden;
    position: relative;
    max-width: 100%;
    padding: 9px 12px;
    text-align: center;
  }
